@import '../../../styles/abstract/constants.scss';

.input {
  color: $gray-300;
  position: relative;
  border-radius: 12px;
  border: none;
}

.input__field {
  border: none;
  color: $black;
  background-color: $gray-300;
  font-family: $family;
  font-size: 13px;
  font-style: normal;
  font-weight: $fw-normal;
  line-height: normal;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  box-sizing: border-box;
  cursor: pointer;

  &::placeholder {
    color: var(--secondary-text-color);
    opacity: 1;
  }

  &:focus {
    outline: 1px solid $gray-light;
  }

  &:invalid {
    outline: 1px solid $red;
    color: $red;
  }

  &_kind {
    &_form {
      width: 396px;
      height: 0;
    }

    &_password {
      width: 396px;
      height: 0;

      &::after {

      }
    }

    &_info {
      width: 396px;
      height: 0;
    }

    &_search {
      width: 396px;
      height: 0;

      &::after {

      }
    }

    &_price {

    }
  }
}
.input__label {
  color: var(--secondary-text-color);
  font-family: Inter, sans-serif;
  font-size: 10px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  margin: 0;
  padding: 0;
}

.input__errorWrapper {
  overflow: hidden;
  margin-bottom: 10px;
  display: none;
}

.active {
  display: block;
}

.input__error {
  display: block;
  border: none;
  min-height: 12px;
  color: var(--accent-color-pink);
  font-family: Inter, sans-serif;
  font-size: 10px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  margin: 0;
  padding: 0;
  text-wrap: normal;
}

.input__errorWrapper_kind_profile {
  overflow: hidden;
  margin: 0;
}

@media screen and (max-width: 600px) {

  .input_kind_form {
    width: calc(100vw - 60px);
  }

  .inputField_kind_search {
    height: 44px;
  }
}


